.loader {
  color: #fff;
  position: fixed;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  left: -9999px;
  top: -9999px;
  width: 0;
  height: 0;
  overflow: hidden;
  z-index: 999999;
}
.loader:after,
.loader:before {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.loader.is-active {
  background-color: rgba(0, 0, 0, 0.85);
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}
.loader-curtain:before,
.loader-curtain:after {
  position: fixed;
  width: 100%;
  top: 50%;
  margin-top: -35px;
  font-size: 70px;
  text-align: center;
  font-family: Helvetica, Arial, sans-serif;
  overflow: hidden;
  line-height: 1.2;
  content: 'Loading...';
}
.loader-curtain:before {
  color: #666;
}
.loader-curtain:after {
  color: #fff;
  height: 0;
  -webkit-animation: curtain 1s linear infinite alternate both;
          animation: curtain 1s linear infinite alternate both;
}
.loader-curtain[data-curtain-text]:not([data-curtain-text='']):before,
.loader-curtain[data-curtain-text]:not([data-curtain-text='']):after {
  content: attr(data-curtain-text);
}
.loader-curtain[data-brazilian]:before {
  color: #f1c40f;
}
.loader-curtain[data-brazilian]:after {
  color: #2ecc71;
}
